import React from 'react'
import Button1 from './components/Button1';
import './App.css';
import Web3 from 'web3';
import detectEthereumProvider from '@metamask/detect-provider';
import {COMMONNAMESERVICE_ABI , COMMONNAMESERVICE_ADDRESS } from './config';

class App extends React.Component {

  constructor(props)  {
    super(props);
    this.state = {commonName: '',
                  accountConnected: '',
                  stringName: '',
                  inputName: '',
                 };

    this.handleChange = this.handleChange.bind(this);
    this.handleChange2 = this.handleChange2.bind(this);



  }


  handleChange(event) {
    this.setState({inputAddress: event.target.value});
  }
  handleChange2(event) {

    this.setState({inputName: event.target.value})
  }



  render() {



  const connectWallet = async () => {

    const provider = await detectEthereumProvider()

    if (provider) {
      const accounts = await provider.request({ method: 'eth_requestAccounts' })
      const account = accounts[0]
      this.setState({accountConnected : account})
      const web3 = new Web3(provider)

      const contract = new web3.eth.Contract(
        COMMONNAMESERVICE_ABI,
        COMMONNAMESERVICE_ADDRESS
        );
      const defaultId = await contract.methods.addressDefaultId(account).call();
      const allURI = await contract.methods.tokenURI(defaultId).call();
      const tokenURI = decodeURIComponent(allURI)

      const str = tokenURI;
      const n = 31;

      const _URI = str.substring(n);

      this.setState({commonName : _URI});
      console.log({_URI})



    console.log('Ethereum successfully detected!', {account})
      }

      else {
      window.addEventListener('ethereum#initialized', detectEthereumProvider, {
      once: true,
      });

// If the event is not dispatched by the end of the timeout,
// the user probably doesn't have MetaMask installed.
  setTimeout(detectEthereumProvider, 3000); // 3 seconds
    }

  }

  const mintName = async () =>  {
    const provider = await detectEthereumProvider()
    const accounts = await provider.request({ method: 'eth_requestAccounts' })
    const account = accounts[0]
    const web3 = new Web3(provider)

    const contract = new web3.eth.Contract(
      COMMONNAMESERVICE_ABI,
      COMMONNAMESERVICE_ADDRESS
      );


    const result = await contract.methods.safeMint( account , this.state.inputName ).send({
        from: account,
      });
    console.log({result})
    }

// const startUp = async () => {
//   const provider = await detectEthereumProvider()
//   const accountsConnected = await provider.request({ method: 'eth_requestAccounts' })
//   const account = accountsConnected[0]
//   const web3 = new Web3(provider)
//
//   const contract = new web3.eth.Contract(
//     COMMONNAMESERVICE_ABI,
//     COMMONNAMESERVICE_ADDRESS
//     );
//   const tokenId = contract.methods.tokenURI(0).call()
//   this.setState({commonName : tokenId})
//
//   }



  return (
    <div className="App-header">

        <h1>Common Name Service</h1>
        <p>The ONE blockchain <br />where everybody knows your name.</p>

          {this.state.accountConnected === '' &&
          <Button1 onClick={connectWallet}>Connect Wallet</Button1>
          }

          {this.state.accountConnected !== '' &&
          <p>Hello {this.state.commonName}</p>
          }

          <h2>Mint your Name</h2>


          <input className="inputField" type="text" value={this.state.inputName} onChange={this.handleChange2} placeholder="Your Name"></input><br />
          <Button1 onClick={mintName}>Mint Name</Button1>
          <div className="footer">
          <a href="https://github.com/kennybetz/hackNFT2022/tree/main/v2"><img src="https://github.githubassets.com/images/modules/site/icons/footer/github-mark.svg"></img></a>
          </div>
    </div>
  );
}

}

export default App;
